@import "colors";

#homeWrap {
  #beamerImage {
    width: 100vw;
    margin-left: calc( calc(100% - 100vw) /2);
    height: 100vh;
  	min-height: 600px;
    background: $gray;
    background-image: url('../bilder/fair.jpg');
  	background-size: cover;
    background-position: 50% 100%;
    background-attachment: fixed;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    z-index: -10;
    margin-top: -151px;
  }

	#beamerWrap {
		width: 100%;
		max-width: 1200px;
    margin-top: -100vh;
		padding: 10px;
		padding-top: 150px;
		padding-bottom: 150px;
		height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
    position: relative;
    
    @media screen and (max-width: $ipad) {
      margin-top: -95vh;

      @media screen and (max-width: $bigPhone) {
        margin-top: -105vh;
        height: 100vh;
        margin-bottom: 10vh;
      }
    }

    .chevron {
      width: 100%;
      display: flex;
      justify-content: center;
      position: absolute;
      bottom: 20px;
      left: 0;
      transition: opacity 0.2s ease;

      .fa {
        color: rgba(255, 255, 255, 0.7);
        font-size: 90px;
        position: relative;
        top: -10px;

        -webkit-animation: fade_move_down 2s ease-in-out infinite;
        animation: fade_move_down 2s ease-in-out infinite;

        /*animated scroll arrow animation*/
        @-webkit-keyframes fade_move_down {
          0%   { top: -10px; opacity: 0; }
          50%  { opacity: 1;  }
          100% { top: 30px; opacity: 0; }
        }
        @keyframes fade_move_down {
          0%   { top: -10px; opacity: 0; }
          50%  { opacity: 1;  }
          100% { top: 30px; opacity: 0; }
        }
      }

      &.after {
        opacity: 0;
      }

      @media screen and (max-width: $smallPhone) {
        bottom: -10px;
      }
    }

		#beamerInfo {
			color: #fff;
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;

			h3 {
				font-size: 25px;
				font-weight: 600;
				letter-spacing: 0.06em;
				text-transform: uppercase;
				padding: 8px 0 8px 0;
			}

			h2 {
				padding: 15px 0 15px 0;
				font-size: 55px;
      }
      
      @media screen and (max-width: $bigPhone) {
        text-align: center;

        h2 {
          font-size: 45px;
        }

        h3 {
          font-size: 20px;
        }
      }
		}

		footer {
			position: relative;
			bottom: -35%;
			width: 100%;
			display: flex;
			justify-content: center;
      transition: opacity 0.2s ease;

      &.after {
        opacity: 0;
      }

			.footCont {
				position: absolute;
				bottom: 0;
				padding: 20px;
				display: flex;
				justify-content: space-around;
        align-items: center;
        
        @media screen and (max-width: $bigPhone) {
          width: 100%;

          @media screen and (max-width: $smallPhone) {
            width: 100vw;
            padding: 10px;
            bottom: -10px;
          }
        }

				p {
					color: white;
					font-size: 15px;
					text-transform: uppercase;
          padding-top: 4px;
          
          @media screen and (max-width: $bigPhone) {
            display: none;
          }
				}

				a {
					color: #fff;
					font-size: 15px;
					text-transform: uppercase;
          cursor: pointer;
          display: flex;

					.fa {
						margin: 0 7px 0 20px;
						font-size: 22px;
          }
          
          @media screen and (max-width: $bigPhone) {
            display: block;

            .fa {
              display: block;
            }
            p {
              font-size: 12px;
              display: block;
            }
          }
				}
			}
		}
	}

  #landingContent {
    min-height: 100vh;

    #generalInfo {
      padding: 90px;
      width: 60%;
      margin: 20px auto 100px auto;
      background-color: $gray;
      border-radius: 2px;
      position: relative;
      color: #fff;

      @media screen and (max-width: $ipad) {
        padding: 45px;

        @media screen and (max-width: $bigPhone) {
          padding: 30px;
          width: 90%;
        }
      }

      h1 {
        font-size: 40px;
        margin-bottom: 10px;
        line-height: 45px;
        letter-spacing: 0.4px;
      }

      p {
        font-size: 14pt;
        line-height: 25px;
        letter-spacing: 0.4px;
      }

      a {
        color: $orange;
        transition: color 0.2s ease;

        &:hover {
          color: $orangeHover;
        }
      }
    }

    .deadspace {
      height: 200px;
    }

    .stickyHeading {
      text-align: center;
      padding: 10px;
      width: 100%;
      // margin-left: calc(calc(-100vw + 100%)/2);
      margin: 15px 0 15px 0;
      color: #fff;
      font-size: 50px;
      letter-spacing: 1px;
      background: $gray;
      background: linear-gradient(90deg, rgba(0,0,0,0) 0%, $gray 20%, $gray 80%, rgba(0,0,0,0) 100%);
      
      @media screen and (max-width: $smallPhone) {
        font-size: 40px;
      }
    }

    #partners {
      width: 100%;
      display: flex;
      flex-flow: row wrap;
      justify-content: space-around;

      .partnerWrap {
        display: flex;
        width: 50%;

        .partner {
          display: flex;
          flex-flow: column wrap;
          justify-content: space-between;
          align-items: center;
          padding: 20px;
          margin: 15px;
          border-radius: 2px;
          width: 100%;
          transition: all 0.2s ease;
          cursor: pointer;
          background-color: $grayLessFade;

          &:hover {
            background: $gray;
            border-radius: 5px;
            text-decoration: none;
            margin: 10px;
            padding: 25px;

            .readMore {
              text-decoration: underline;
            }

            .partnerLogo {
              padding: 15px;
            }
          }

          .partnerLogo {
            width: auto;
            height: 150px;
            max-width: 100%;
            transition: all 0.2s ease;
            padding: 10px;
          }

          .partnerInfo {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: fit-content;
            color: #fff;

            .readMore {
              transition: all 0.2s ease;
            }

            p {
              padding-bottom: 5px;
            }
          }
        }

        @media screen and (max-width: $bigPhone) {
          width: 100%;
        }
      }
    }

    #silverPartners {
      margin-top: 100px;
      background: $navyFade;
      background: linear-gradient(90deg, rgba(0,0,0,0) 0%, $navyFade 20%, $navyFade 80%, rgba(0,0,0,0) 100%);
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      h2 {
        padding-top: 10px;
        color: #fff;
        font-size: 28px;
      }

      .silverPartners {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        @media screen and (max-width: $bigPhone) {
          flex-wrap: wrap;
          padding: 25px 0 25px 0;
        }

        .partnerWrap {
          display: flex;
          width: 17%;
          padding: 15px;
          height: 150px;

          @media screen and (max-width: $ipad) {
            padding: 10px;
            width: 20%;
            height: 120px;
          
            @media screen and (max-width: $bigPhone) {
              width: 50%;
              flex-wrap: wrap;
            }
          }

          .partner {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 30px;
            border-radius: 2px;
            width: 100%;
            transition: all 0.2s ease;
            cursor: pointer;

            @media screen and (max-width: $ipad) {
              padding: 10px;
              background-color: $gray;
            }

            &:hover {
              background-color: $gray;
              border-radius: 5px;
              // padding: 28px;

              .partnerLogo {
                padding: 5px;
              }
            }

            .partnerLogo {
              height: auto;
              width: 100%;
              transition: all 0.2s ease;
            }
          }
        }
      }
    }
  }

  #newsContainer {
    margin-top: 90px;
    width: 100%;
    display: flex;
    justify-content: center;

    #newsPreview {
      width: 45%;
      padding: 15px;
      border: $border;
      background-color: $background;
    }
  }
}
